import Vue from 'vue';
import App from './App.vue';
import runtime from 'serviceworker-webpack-plugin/lib/runtime';

import './style/index.scss';

Vue.config.productionTip = false;

// Register service workers
if ('serviceWorker' in navigator) {
  runtime.register();
}

new Vue({
  render: h => h(App),
})//.$mount('#app')
